import Endpoint from '@/services/Endpoint'

export default {
  accountStatuses (payload) {
    return Endpoint().get('/s1/account/accountStatus', {
      params: payload
    })
  },
  createAccountStatus (payload) {
    return Endpoint().post('/s1/account/accountStatus', payload)
  },
  modifyAccountStatus (payload) {
    return Endpoint().patch('/s1/account/accountStatus', payload)
  }
}
